    turnBack() {
      this.$router.push('/me/ads/list');
    },

    selectIndexEvent(id) {
      let selectIndexData = this.selectIndexData;
      let index = selectIndexData.indexOf(id);

      if (index == -1) {
        this.selectIndexData.push(id);
      } else {
        this.selectIndexData.splice(index, 1);
      }
    },

    getCategoryInfo(id) {
      let data = {
        category_id: id
      };
      GET_ADS_CATEGORY_INFO(data).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.adsData = res.message;
        } else {
          console.log(res.msg);
        }
      }).then(err => {
        console.log(err);
      });
    },

    leiSwitchEvent(value) {
      this.switchValue = value;

      if (value == 1) {
        this.$msgbox({
          title: "Notice",
          message: "This feature will cost you an additional ￥20",
          dangerouslyUseHTMLString: false,
          type: "warning",
          center: true,
          showCancelButton: true,
          cancelButtonText: "Cancel",
          confirmButtonText: "Confirm",
          "round-button": true,

          callback(action) {
            console.log(action);

            if (action === 'confirm') {
              console.log('用户点击确定');
            }
          }

        });
      }
    },

